
import InlineXNumber from 'vux/src/components/inline-x-number/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'

export default
{

components: {
InlineXNumber,
CheckIcon
},
data () {
return {
goodCount: 0,
goodCheck:false
}
},
props:{
title: {
type: String,
required: false,
default: ''
},
imgurl: {
type: String,
required: false,
default: ''
},
attribute: {
type: String,
required: false,
default: ''
},
salePrice: {
type: Number,
required: false,
default: ''
},
specification: {
type: String,
required: false,
default: ''
},
count: {
type: Number,
default: 0
},
check:{
type: Boolean,
default: false
},
memberPrice:Number,
isExclusive:Boolean

},
created () {
this.goodCount = this.count;
this.goodCheck = this.check;
},

watch: {
count (newval) {
if (this.goodCount !== newval) {
this.goodCount = newval
console.log(this.goodCount)
}
},
goodCount (newval) {
this.$emit('countChange', newval)
},
check (newval) {
if (this.goodCheck !== newval) {
this.goodCheck = newval
}
},
goodCheck (newval) {
this.$emit('checkChange', newval)
}
}
}



